/**
 * Http
 * @const {number}
 */
const DEFAULT_HTTP_TIMEOUT = 5000;

/**
 * @enum {string}
 */
let HttpError = {
    TIMEOUT: 'timeout',
    ERROR: 'error',
    ABORT: 'abort'
};

/**
 * Http
 * XMLHttpRequest
 * @type {HttpResponse}
 */
let HttpResponse = cc.Class({

    ctor: function(){
        /**
         * @type {XMLHttpRequest}
         */
        this.xhr_ = null;
        /**
         * 
         * @type {?HttpError}
         * @private
         */
        this.error_ = null;
    },

    init: function (xhr) {
        this.xhr_ = xhr;
    },

    /**
     * 
     * @returns {boolean}
     */
    isOk: function() {
        var xhr = this.xhr_;
        return xhr.readyState == 4 && (xhr.status >= 200 && xhr.status <= 207);
    },

    /**
     * Response
     * @returns {(string|ArrayBuffer|Blob|Object|Document)}
     */
    getBody: function() {
        return this.xhr_.response;
    },

    /**
     * @param {!HttpError} error
     */
    setError: function(error) {
        this.error_ = error;
    },

    /**
     * 'timeout', 'error', 'abort'
     * isOk()false
     * @returns {?HttpError}
     */
    getError: function() {
        return this.error_;
    },

    /**
     * Http Response Header
     * @returns {object.<string, Array.<string>>} key
     */
    getHeaders: function () {
        // todo: 
    },

    /**
     * Http Response Header
     * @param {string} name Header
     * @returns {Array.<string>} []
     */
    getHeader: function (name) {
        // todo: 
    }
});

/**
 * XmlHttpRequest
 * @param {XMLHttpRequest} xhr
 * @param {function} callback
 * @private
 */
let registerEventsForXmlHttpRequest_ = function(xhr, callback) {
    var r = new HttpResponse();
    r.init(xhr);

    xhr.onreadystatechange = function (evt) {
        if (xhr.readyState == 4 ) {
            callback(r);
        }
    };

    xhr.ontimeout = function(evt) {
        r.setError(HttpError.TIMEOUT);
        callback(r);
    };

    xhr.onerror = function (evt) {
        r.setError(HttpError.ERROR);
        callback(r);
    };

    xhr.onabort = function (evt) {
        r.setError(HttpError.ABORT);
        callback(r);
    };
};

/**
 * Http GET
 * @param {string} url
 * @param {function} callback callback(HttpResponse)
 * @param {number} opt_timeout 
 */
let httpGet = function(url, callback, opt_timeout) {
    var xhr = cc.loader.getXMLHttpRequest();
    xhr.timeout = opt_timeout ? opt_timeout : DEFAULT_HTTP_TIMEOUT;
    if(callback){
        registerEventsForXmlHttpRequest_(xhr, callback);
    }
    xhr.open('GET', url, true);

    // xhr.setRequestHeader("Accept-Encoding","gzip,deflate");
    // open()jsburl.jsonJSON
    //xhr.responseType = 'arraybuffer';

    xhr.send();
};

/**
 * Http Post
 * @param url
 * @param data{String} 
 * @param callback
 * @param opt_timeout
 */
let httpPost = function (url, data, callback, opt_timeout) {
    let xhr = cc.loader.getXMLHttpRequest();
    xhr.timeout = opt_timeout ? opt_timeout : DEFAULT_HTTP_TIMEOUT;
    if (callback){
        registerEventsForXmlHttpRequest_(xhr, callback);
    }

    xhr.open('POST', url, true);
    // json(Expressjson)
    // xhr.setRequestHeader("Content-Type", "application/json");
    xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded; charset=UTF-8");

    cc.log("===>httpPost: " + typeof data + " | " + JSON.stringify(data));

    // xhr.setRequestHeader("Accept-Encoding","gzip,deflate");
    // open()jsburl.jsonJSON
    //xhr.responseType = 'arraybuffer';

    xhr.send(data);
};

module.exports = {
    httpGet: httpGet,
    httpPost: httpPost
};