"use strict";

/**
 * BaseRequest BaseResponseBaseProtocol
 */
let BaseProtocol = cc.Class({
    ctor: function () {
        /**
         * 
         */
        this.act = '';

        /**
         * sequence_id
         */
        this.seq = 0;

        /**
         * 0
         */
        this.err = 0;

        /**
         * 
         */
        this.is_async = false;
    }
});

/**
 * 
 */
let BaseRequest = cc.Class({
    extends: BaseProtocol
});

/**
 * BaseRequest
 * @class BaseResponse
 * @extends BaseProtocol
 */
let BaseResponse = cc.Class({
    extends: BaseProtocol,

    /**
     * BaseResponse
     */
    loadData: function (data) {
        var key;
        for (key in data) {
            if(!this.hasOwnProperty(key)){
                continue;
            }

            if(data[key] !== undefined && data[key] !== null){
                this[key] = data[key];
            }
        }
    }
});

