let LoginRequest = cc.Class({
    extends: BaseRequest,

    ctor: function () {
        this.act = 'login';

        /**
         * facebookaccessTokenUUID
         */
        this.token = '';

        /**
         * token0:1:facebook
         */
        this.origin = 0;

        /**
         * : android/ios/winphone/pc
         */
        this.os = '';

        /**
         * 
         */
        this.osVersion = '';

        /**
         * ,  iPhone8,2, SM-G 9280
         */
        this.deviceModel = '';

        /**
         * ID
         */
        this.channelId = 0;

        /**
         * Ios
         */
        this.idfa = '';

        /**
         * id
         */
        this.androidId = '';

        /**
         * Googlegoogle play
         */
        this.googleAid = '';

        /**
         * 
         */
        this.appVersion = '';

        /**
         * package namebundle id
         */
        this.packName = '';


        /**
         * 
         * @type {string}
         */
        this.language = '';

        this.locale = "";

        this.uid = 0;

    }
});

let LoginResponse = cc.Class({
    extends: BaseResponse,

    ctor: function () {
        this.act = 'login';

        /**
         * token
         */
        this.token = '';

        this.self = {
            isBlack: false,
            chessDic: {}
        };

        this.other = {
            isBlack: false,
            chessDic: {},
            uid: 0
        };

        this.order = 0;

        this.rid = 0;

        this.isReconn = false;  // 

    }
});
